'use client';

import React, { useState } from 'react';
import { useRouter } from 'next/navigation';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { z } from 'zod';
import { Button } from '@/components/Button';
import { Input } from '@/components/Input';
import { Wave } from '@/components/Wave';
import { authApi } from '@/lib/api/auth';
import { useAuthStore } from '@/lib/store';

const loginSchema = z.object({
  email: z.string().email('Invalid email address'),
  password: z.string().min(6, 'Password must be at least 6 characters'),
  rememberMe: z.boolean().optional(),
});

type LoginFormData = z.infer<typeof loginSchema>;

export default function LoginPage() {
  const router = useRouter();
  const login = useAuthStore((state) => state.login);
  const [error, setError] = useState<string>('');
  const [isLoading, setIsLoading] = useState(false);

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm<LoginFormData>({
    resolver: zodResolver(loginSchema),
  });

  const onSubmit = async (data: LoginFormData) => {
    try {
      setIsLoading(true);
      setError('');
      const response = await authApi.login(data.email, data.password);
      login(response.user.email, response.access_token);
      router.push('/movies');
    } catch (err: any) {
      setError(err.response?.data?.message || 'Login failed. Please try again.');
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <section className="hero hero--auth flex flex-col items-center justify-center text-center px-4 py-[120px] pb-[160px] relative min-h-[520px]">
      <h1 className="hero__title text-[64px] font-bold mb-7 text-white leading-none tracking-[-0.02em] m-0">
        Sign in
      </h1>
      <form
        className="form form--auth flex flex-col gap-[18px] items-center mt-2 w-full"
        onSubmit={handleSubmit(onSubmit)}
      >
        <Input
          type="email"
          placeholder="Email"
          {...register('email')}
          error={errors.email?.message}
        />
        <Input
          type="password"
          placeholder="Password"
          {...register('password')}
          error={errors.password?.message}
        />
        <label className="remember flex gap-2.5 items-center text-white/70 cursor-pointer">
          <input
            type="checkbox"
            {...register('rememberMe')}
            className="w-3.5 h-3.5"
          />
          Remember me
        </label>
        {error && (
          <p className="text-red-400 text-sm">{error}</p>
        )}
        <Button type="submit" disabled={isLoading}>
          {isLoading ? 'Logging in...' : 'Login'}
        </Button>
      </form>
      <Wave />
    </section>
  );
}

